import React, { Component } from "react";

class index extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentWillMount() {
    window.addEventListener("resize", event => {
      this.setState({
        width: event
      });
    });
  }
  getRows = () => {
    var matches = [];
    for (var i in this.props.matches) matches.push(this.props.matches[i]);
    return matches.map((item, i) => {
      return (
        <tr key={i}>
          <td>{item.id}</td>
          <td>{item.season}</td>
          <td>{item.city}</td>
          <td>{item.date}</td>
          <td>{item.team1}</td>
          <td>{item.team2}</td>
          <td>{item.toss_winner}</td>
          <td>{item.toss_decision}</td>
          <td>{item.result}</td>
          <td>{item.dl_applied}</td>
          <td>{item.winner}</td>
          <td>{item.win_by_runs}</td>
          <td>{item.win_by_wickets}</td>
          <td>{item.player_of_match}</td>
          <td>{item.venue}</td>
          <td>{item.umpire1}</td>
          <td>{item.umpire2}</td>
          <td>{item.umpire3}</td>
        </tr>
      );
    });
  };
  getTabletRows = () => {
    return this.props.matches.map((item, i) => {
      return (
        <tr key={i}>
          <td>
            <div className="row">
              <div class="col-sm heading-th">id:</div>
              <div class="col-sm">{item.id}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">season:</div>
              <div class="col-sm">{item.season}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">city:</div>
              <div class="col-sm">{item.city}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">date:</div>
              <div class="col-sm">{item.date}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">team1:</div>
              <div class="col-sm">{item.team1}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">team2:</div>
              <div class="col-sm">{item.team2}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">toss winner:</div>
              <div class="col-sm">{item.toss_winner}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">toss decision:</div>
              <div class="col-sm">{item.toss_decision}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">result:</div>
              <div class="col-sm">{item.result}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">dl applied:</div>
              <div class="col-sm">{item.dl_applied}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">winner:</div>
              <div class="col-sm">{item.winner}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">win byruns:</div>
              <div class="col-sm">{item.win_by_runs}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">win by wickets:</div>
              <div class="col-sm">{item.win_by_wickets}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">player of match:</div>
              <div class="col-sm">{item.player_of_match}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">venue:</div>
              <div class="col-sm">{item.venue}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">umpire1:</div>
              <div class="col-sm">{item.umpire1}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">umpire2:</div>
              <div class="col-sm">{item.umpire2}</div>
            </div>
          </td>
          <td>
            <div className="row">
              <div class="col-sm heading-th">umpire3:</div>
              <div class="col-sm">{item.umpire3}</div>
            </div>
          </td>
        </tr>
      );
    });
  };
  render() {
    return (
      <div>
        <div id="pagination">
          <span>{`${this.props.matches[this.props.matches.length - 1].id /
            20} - ${this.props.matches.length} of 100`}</span>
          <a
            onClick={() => {
              if (this.props.page > 0) {
                this.props.decMatches(this.props.page - 1);
              }
            }}
          >
            <span class="fa fa-chevron-left"></span>
          </a>
          <a
            onClick={() => {
              if (this.props.page >= 0) {
                this.props.incMatches(this.props.page + 1);
              }
            }}
          >
            <span class="fa fa-chevron-right"></span>
          </a>
        </div>
        <table className="table table-bordered">
          <thead>
            <tr className="heading-main">
              <th>id</th>
              <th>season</th>
              <th>city</th>
              <th>date</th>
              <th>team1</th>
              <th>team2</th>
              <th>toss winner</th>
              <th>toss decision</th>
              <th>result</th>
              <th>dl applied</th>
              <th>winner</th>
              <th>win by runs</th>
              <th>win by wickets</th>
              <th>player of match</th>
              <th>venue</th>
              <th>umpire1</th>
              <th>umpire2</th>
              <th>umpire3</th>
            </tr>
          </thead>
          <tbody>
            {window.innerWidth > 1100 ? this.getRows() : this.getTabletRows()}
          </tbody>
        </table>
      </div>
    );
  }
}

export default index;
